.services {
    width: 100vw;
    height: 100vh;
    
    .section-1{
        margin-top: 300px;
        width: 100vw;
        .section-1-services{

            display: grid;
            grid-template-columns: 50% 50%;
            grid-template-rows: 50% 50%;
            gap: 30px;
            margin: 0 auto;

            @media only screen and (max-width: 600px) {
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
               
            }

            .service {
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 0 auto;
                gap: 20px;
                width: 80%;


                .icon-flex, .icon-wrapper {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
                .icon-wrapper {
                    border-radius: 50%;
                    overflow: hidden;
                    border: 2px solid $color-2;
                    font-size: 2rem;
                    width: 90px;
                    height: 90px;
                }
                .right{

                    h3 {
                        font-size: 1.5rem;
                        margin-bottom: 10px;
                        color: $color-2;
                    }

                    p{
                        font-size: 1rem;
                        font-weight: lighter;
                    }
                }
            }
        }
    }
    .section-2{
        width: 100%;
        height: 100%;
        margin-top: 150px;
        display: flex;
        align-items: center;
        justify-content: center;
        @media only screen and (max-width: 600px){
            margin-bottom: 500px;
            margin-top: 500px;
        }
        .section-2-chose{
            width: 95%;
            height: 80%;
            gap: 40px;
            display: flex;
            justify-content: center;
            align-items: center;
            @media only screen and (max-width: 600px){
                height: 60%;
                flex-direction: column;
            }
           
            .chose-card{
                width: 95%;
                height: 100%;
                background: $color-2;
                border-radius: 25px;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
               
                h2 {
                    font-size: 1rem;
                    letter-spacing: 1px;
                    font-weight: lighter;
                    margin: 25px auto;
                }

                .separation{
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 2rem;
                    margin: 50px auto;
                }
                .all-list{
                    

                    .list{
                        margin: 20px 0;
                        .list-container{
                            display: flex;
                            gap: 20px;
                        }
                    }
                }

            }

            .chose-card-2{
                width: 95%;
                height: 100%;
                background: $color-3;
                color: $black;
                border-radius: 25px;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                h2 {
                    font-size: 1rem;
                    letter-spacing: 1px;
                    font-weight: lighter;
                    margin: 25px auto;
                }

                .separation{
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 2rem;
                    margin: 50px auto;
                }
                .all-list{
                    

                    .list{
                        margin: 20px 0;
                        .list-container{
                            display: flex;
                            gap: 20px;
                        }
                    }
                }

            }
            
        }
    }
    .section-3{
        height: 100%;
        width: 100%;
        background: $color-2;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        

        .section-3-container{
            display: flex;
            align-items: left;
            flex-direction: column;
            gap: 20px;
            font-weight: lighter;

            .question-item{
                background: transparent;
                margin-bottom: 1rem;
                padding: 1rem;

            }
            .question-link{
                font-size: 1.5rem;
                color: $black;
                text-decoration: none;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;

            }

            a:hover{
                color: $color-1;
                transition: 0.2s;
            }

            .answer{
                max-height: 0;
                overflow: hidden;
                position: relative;
                background: transparent;
                transition: max-height 650ms;
            }

            .answer::before{
                content: "";
                position: absolute;
                width: .3rem;
                height: 70%;
                border-radius: 25px;
                background: $color-3;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
            }

            .answer p{
                padding: 2rem;
            }

            .question-item:target .answer{
                max-height: 20rem;
            }
            
        }
    }

}