.apple{
    position: absolute;
    height: 100vh;
    width: 100vw;
    font-size: 1.4rem;
    .section-1{
        .section-1-container{
            display: grid;
            grid-template-columns: 60% 40%;
            justify-content: center;
            align-items: center;
            margin: 300px auto;
            width: 90%;
            height: 50%;
            @media only screen and (max-width: 600px){
                display: block;
                margin-bottom: 150px;
                
            }
            .left{
                height: 200%;
                width: 90%;
                background: url(../../assets/img/slpc7.jpg);
                background-position: center;
                background-size: cover;
                @media only screen and (max-width: 600px){
                    height: 300px;
                    margin-bottom: 50px;
                }
            }
            .right{
                margin: 0 auto;
                height: 100%;
                width: 80%;
                h2{
                font-size: 2.5rem;
                margin-bottom: 20px;
                position: relative;

                }
                h2:before {
                content: "";
                position: absolute;
                width: 92%;
                height: 1px;
                background: #fefeff;
                z-index: 5;
                bottom: 0%;

                }

                p{
                font-size: 1.2rem;
                text-align: justify;
                text-justify: inter-word;
                }
            }
        }

        .section-1-bottom{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            margin-bottom: 100px;
            
            .description{
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                background: $color-2;
                width: 100%;
                height: 100%;
                margin-bottom: 20px;
                p{
                    font-size: 2rem;
                    margin: 50px auto;
                    @media only screen and (max-width: 600px){
                        font-size: 1.2rem;
                        margin-left: 30px;
                    }
                }
            }
            h3 {
                font-weight: lighter;
                margin-top: 50px;
            }
            .button-nav{
                margin-bottom: 100px;
            }
          
        }
    }
}