.home {
  position: absolute;
  height: 100vh;
  width: 100vw;
  font-size: 1.4rem;

  .section-1{
    
    
    .section-1-container{
      display: grid;
      grid-template-columns: 40% 60%;
      justify-content: center;
      align-items: center;
      margin: 300px auto;
      width: 100%;
      height: 50%;

      @media only screen and (max-width: 600px) {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        gap: 50px;
        height: 100%;
        margin-bottom: 200px;
       
      }

      

      .left {
        margin: 0 auto;
        height: 100%;
        width: 80%;
        h2{
          font-size: 2.5rem;
          margin-bottom: 50px;
          position: relative;

        }
        h2:before {
          content: "";
          position: absolute;
          width: 50%;
          height: 1px;
          background: #fefeff;
          z-index: 5;
          bottom: 0%;

          }

        p{
          font-size: 1.2rem;
          text-align: justify;
          text-justify: inter-word;
        }
      }
  
      .right{
        height: 200%;
        width: 80%;
        background: url(../../assets/img/slpc1.jpg);
        background-position: center;
        background-size: cover;

        @media only screen and (max-width: 600px) {
          height: 300px;
          width: 80%;
         
        }

      }
    }

    .section-1-bottom{
      margin-bottom: 200px;
      background: $color-2;
      height: 200px;
      
      
      .experience{
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 1.2rem;
        margin: 0 50px;
        background: $color-2;
        height: 100%;

        @media only screen and (max-width: 600px) {
          font-size: 0.8rem;
          gap: 25px;

          h3{
            font-size: 2rem;
          }
        }

        h3{
          font-size: 2.5rem;
          position: relative;
          z-index: 0;
        }
        h3:after {
          content: "";
          height: 30px;
          width: 30px;
          border: 1px $black solid;
          border-radius: 50%;
          position: absolute;
          top: 0px;
          left: -10px;
          z-index: -1;
        }
        P{
          font-style: italic;
          font-family: $font-1;
        }
      }
    }
    
  }

  .section-2 {
    height: 100%;
    width: 100%;
    opacity: 0.7;
    display: flex;
    align-items: center;
    justify-content: center;

    .section-2-container{
      background: url(../../assets/img/slpc2.jpg);
      background-position: center;
      background-size: cover;
      width: 95%;
      height: 100%;
      display: flex;
      flex-direction: column;
      text-align: center;
      align-items: center;
      justify-content: center;

      @media only screen and (max-width: 600px) {
        width: 100%;
        height: 85%;
       
      }

      .about{
        width: 50%;
        height: 70%;
        background: $color-2;
        color: #fefeff;
        display: flex;
        text-align: center;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        @media only screen and (max-width: 600px) {
          width: 65%;
          height: 65%;
          
        }
        h2{
          font-size: 3rem;
          @media only screen and (max-width: 600px) {
            font-size: 2rem;
           
          }
        }

        p{
          font-size: 1rem;
          width: 70%;
          margin: 30px 0;

          @media only screen and (max-width: 600px) {
            font-size: 0.8rem;
          }

        }

        .button{
          background: $black;
          color: $color-3;
          height: 55px;
          width: 126px;
          border-radius: 50px;
          margin-top: 20px;
          font-size: 1rem;
          cursor: pointer;
          transition: 0.2s;
          border: none;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
  }

  .section-3{
    margin-top: 100px;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 50% 50%;
    margin: 100px auto;
    @media only screen and (max-width: 600px) {
      display: block;
    }

    .left{
      background: $color-2;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      color: $black;
      @media only screen and (max-width: 600px) {
        height: 80%;
      }
      p{
        width: 60%;
        font-size: 3rem;
        @media only screen and (max-width: 600px) {
          font-size: 2rem;
        }
      }

    }
    .right {
      background: url(../../assets/img/slpc5.jpg);
      background-position: center;
      background-size: cover;
      @media only screen and (max-width: 600px) {
        height: 80%;
      }
    }
  }

  .section-4 {
    display: grid;
    grid-template-columns: 45% 55%;
    margin: 0 auto;
    width: 95%;
    height: 100%;
    @media only screen and (max-width: 600px) {
      display: block;
      margin-top: 400px;
      gap: 20px;
      
    }

    .map{
      width: 100%;
      height: 80%;
      background: url(../../assets/img/maps.png);
      background-position: center;
      background-size: cover;
      z-index: -5;
      @media only screen and (max-width: 600px) {
        height: 60%;
        width: 80%;
        margin: 0 auto;
      }
    }

    .contact{
      display: flex;
      width: 100%;
      height: 80%;
      flex-direction: column;
      @media only screen and (max-width: 600px) {
        margin: 0 auto;
      }
    }
    
  }


}