.scroll-bottom {
  display: flex;
  justify-content: center;
  a {
      top: 50%;
    font-size: 3rem;
    color: rgb(184, 183, 183);
    transition: 0.25s;
    user-select: none;
    padding: 0 14px;

    &:hover {
      font-size: 4rem;
    }
  }

  .up {
    left: 15px;
  }

  .down {
    right: -24px;
  }
}
