.about {
    position: absolute;
    height: 100vh;
    width: 100vw;
    font-size: 1.4rem;
    .section-1{
      margin: 300px auto;
      display: flex;
      text-align: center;
      justify-content: center;
      flex-direction: column;
      width: 100%;
      height: 100%;
      @media only screen and (max-width: 600px){
        margin-top: 400px;
    }
      
      .section-1-container{
        display: flex;
        text-align: left;
        flex-direction: column;
        margin: 0 auto;
        padding: 50px;
        background: $color-2;
        h2{
          font-size: 2.5rem;
          margin-bottom: 50px;
          position: relative;
    
        }
        h2:before {
          content: "";
          position: absolute;
          width: 45%;
          height: 1px;
          background: #fefeff;
          z-index: 5;
          bottom: 0%;
    
          }
      }
    }

    .contact-info{
      display: flex;
      justify-content: space-around;
      align-items: center;
      margin: 50px 0;
     
    }
  }